import React, { Component } from 'react';
import { postAction } from '@/axios';
import moment from 'moment';
import { message, Table, Divider, Modal } from 'antd';
import { withRouter } from 'react-router-dom';
import FilterBox from './filterBox';
import styles from './index.module.less';
import {hasButtonPermission} from '@/util'
import HandleAllModal from './handleAllModal';
import PreviewDoc from '@/pages/common/PreviewDoc';
const statusList = [//状态可选项
    // {label:"全部",value:0,color:""},
    {label:"审核中",value:1,color:"rgb(250, 219, 20)"},
    {label:"审核通过",value:2,color:"rgb(82, 196, 26)"},
    {label:"审核退回",value:3,color:"rgb(250, 84, 28)"},
    {label:"审核超时",value:4,color:"yellow"}
]
const auditTypeList = [//审核类型
    {label:"填报计划审核",value:1},
    {label:"创建表单审核",value:2},
    {label:"填报结果审核",value:3},
]
class AuditManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            filterValue:{},
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:"60px",
                },{
                    title:"编号",
                    dataIndex:"nodeTaskCode",
                    render: (text) => (<span title={text}>{text}</span>)
                },{
                    title:"业务名称",
                    dataIndex:"nodeTaskName",
                    render:(text)=>(<span title={text}>{text}</span>)
                },{
                    title:"提交人",
                    dataIndex:"creatorName",
                    render: (text) => (<span title={text}>{text}</span>)
                },{
                    title:"接收时间",
                    dataIndex:"taskStartTime",
                    render:(text)=>{
                        let str = moment.unix(text/1000).format("YYYY-MM-DD HH:mm:ss");
                        if(text){
                            return (<span title={str}>{str}</span>)
                        }else{
                            return "-"
                        }
                    }
                },{
                    title:"完成时间",
                    dataIndex:"taskEndTime",
                    render:(text)=>{
                        let str =  moment.unix(text/1000).format("YYYY-MM-DD HH:mm:ss");
                        if(text){
                            return (<span title={str}>{str}</span>)
                        }else{
                            return "-"
                        }
                    }
                },{
                    title:"审核类型",
                    dataIndex:"processType",
                    render:(text) => {
                        const type = auditTypeList.find(item => item.value === text);
                        let str = type?type.label:"未知类型";
                        return  (<span title={str}>{str}</span>)
                    }
                },{
                    title:"状态",
                    dataIndex:"appNodeTaskStatus",
                    render:(text,record) => {
                        if(record.isTimeOut===1){
                            return <React.Fragment><i style={{width:8,height:8,display:"inline-block",borderRadius:"50%",marginRight:5,background:"purple"}}></i>审核超时</React.Fragment>
                        }
                        const status = statusList.find(item => item.value === text);
                        return status?<React.Fragment><i style={{width:8,height:8,display:"inline-block",borderRadius:"50%",marginRight:5,background:status.color}}></i>{status.label}</React.Fragment>:"未知状态"
                    }
                },{
                    title:"操作",
                    width:"120px",
                    render:(text,record)=>{
                        if(record.appNodeTaskStatus===3){//退回状态，只能查看
                            return hasButtonPermission('audit003')? 
                            <span className="operation-link" onClick={()=>{this.lookUp(record)}}>查看</span>:null
                        }else if(record.appNodeTaskStatus===2){//审核通过，能查看或者预览
                            if(record.processType===1){//填报计划，只能查看
                                return hasButtonPermission('audit003')? 
                                <span className="operation-link" onClick={()=>{this.lookUp(record)}}>查看</span>:null
                            }else{
                                return hasButtonPermission('audit001') ?
                                <span className="operation-link" onClick={()=>{this.preview(record)}}>预览打印</span>:null
                            }
                        }else{//审核中，能预览/审核
                            if(record.processType===1){//填报计划，只能审核
                                return hasButtonPermission('audit002') ?<span className="operation-link" onClick={()=>{this.audit(record)}}>审核</span>:null
                            }else{
                                return (
                                    <React.Fragment>
                                        {
                                            hasButtonPermission('audit002') && <React.Fragment>
                                                <span className="operation-link" onClick={()=>{this.audit(record)}}>审核</span>
                                                <Divider type="vertical"/>
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission('audit001')&&
                                            <span className="operation-link" onClick={()=>{this.preview(record)}}>预览打印</span>
                                        }
                                    </React.Fragment>
                                )
                            }
                        }
                    },
                }
            ],
            dataSource:[
                // {sortIndex:"1",nodeTaskCode:"9927",nodeTaskName:"教师基本信息收集表",userName:"邵明",taskEndTime:"2018-03-18 19:00:00",taskStartTime:"2018-01-11 19:00:00",processType:1,appNodeTaskStatus:2,id:"1"},
            ],
            pagination:{
                current: 1,
                pageSize: 10,
                total:0,
                showTotal: (total) => (`共${total}条数据`),
                showQuickJumper:true,
                // showSizeChanger:true,
            },
            businessId:"",//预览用id,
            showItem:{},
            selectedRowKeys:null,
            selectedRows: null,
            handleAllModalStatus: false
        }
    }
    componentDidMount(){
        this.getApproveNodeTaskList({});
    }
    //查询审核任务列表
    getApproveNodeTaskList(data,page){
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let url = `/ybt/wf/approveTask/getApproveNodeTaskList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //设置分页数据
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //设置表格数据
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                // console.log(dataSource);
                this.setState({
                    dataSource,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //搜索
    handleSearch = (filterValue, type) => {
        if(type === 'reset'){
            this.setState({
                pagination:{
                    ...this.state.pagination,
                    current: 1,
                }
            }, () => {
                this.setState({filterValue});
                this.getApproveNodeTaskList(filterValue);
            })
            return false;
        }
        // console.log("筛选条件：",filterValue);
        this.setState({filterValue});
        this.getApproveNodeTaskList(filterValue);
    }
    handleTableChange=(pagination)=>{
        this.setState({
            pagination:pagination
        },()=>{
            this.getApproveNodeTaskList(this.state.filterValue,"page");
        })
    }
    //预览
    preview = (record) => {
        this.setState({
            showPDF:true,
            businessId:record.businessId,//预览用id
            showItem:record
        })
    }
    //查看
    lookUp = (record) => {
        // message.info("跳页面查看");
        this.props.history.push("/auditManagement/lookUp?id="+record.processNodeTaskId)
    }
    //审核
    audit = (record) => {
        // console.log(record);
        this.props.history.push("/auditManagement/audit?id="+record.processNodeTaskId)
    }

    // 批量处理
    handleAll = () => {
        let selectedRows = this.state.selectedRows;
        if (!selectedRows || !selectedRows[0]) {
            message.warn('请勾选需要批量审核的内容！！！')
            return false
        } else {
            let bool = true
            selectedRows.forEach(
                (item) => {
                    if (item.appNodeTaskStatus !== 1) {
                        bool = false;
                        return false
                    }
                }
            )
            if (!bool) {
                message.warn('只能处理“审核中”状态下的批处理，存在错误勾选项，请重新勾选！！！', 8)
                return false
            } else {
                this.setState({
                    handleAllModalStatus: true
                })
            }
        }
    }

    // 隐藏批量处理弹出框
    hideHandleAllModal = () => {
        this.setState({
            handleAllModalStatus: false
        })
    }

    // 批处理完成重新加载
    reloadTable = () => {
        this.getApproveNodeTaskList(this.state.filterValue,"page")
    }

    //清空当前的所有勾选项
    clearAllCheckBox = () => {
        this.setState({
            selectedRowKeys: null,
            selectedRows: null
        })
    }
    
    render() {
        const rowSelection = {
            selectedRowKeys:this.state.selectedRowKeys,
            onChange: (selectedRowKeys, selectedRows) => {
              this.setState({
                selectedRowKeys,
                selectedRows
              })
            }
          };

        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.cardTitle}>
                        审核管理
                    </div>

                    <FilterBox 
                        updateFilterValue = {(filterValue, type) => {this.handleSearch(filterValue, type)}} 
                        handleAll = {this.handleAll}
                        clearAllCheckBox = {this.clearAllCheckBox}
                    />

                    <div className={styles.table}>
                        <Table
                            rowKey={recode => recode.nodeTaskCode}
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            pagination={this.state.pagination}
                            onChange={this.handleTableChange}
                            rowSelection = {rowSelection}
                        />
                    </div>

                </div>

                <Modal
                    visible={this.state.showPDF}
                    // title="预览"
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.showItem.businessId} type={this.state.showItem.processType===2?'formId':'formTaskId'}/>
                </Modal>

                <HandleAllModal 
                    handleAllModalStatus = {this.state.handleAllModalStatus} 
                    hideHandleAllModal = {this.hideHandleAllModal}
                    selectedRows = {this.state.selectedRows}
                    reloadTable = {this.reloadTable}
                />
                
            </div>
        )
    }
}

export default withRouter(AuditManagement);