import React, { Component } from 'react';
import {Modal,Radio,Input,message} from 'antd';
import { postAction } from '@/axios';

const { TextArea } = Input;

class HandleAllModal extends Component{
    state = {
        value: 2,
        textAreaValue: ''
    }

    radioOnChange = e => {
        this.setState({
          value: e.target.value,
        })
      };

    textAreaOnChange = ({ target: { value } }) => {
        console.log(value)
        this.setState({ textAreaValue: value });
      };

    sure = () => {
        let selectedRows = this.props.selectedRows;
        let arr = [];
        if(this.state.value===3 && this.state.textAreaValue===""){//审核退回必须要填写审核意见
            message.warning("审核退回必须要填写审核意见!");
            return false;
        }
        selectedRows.forEach(
            (item) => {
                arr.push({
                    businessId: item.businessId,
                    businessType: item.businessType,
                    processNodeTaskId:item.processNodeTaskId,
                    appNodeTaskStatus:this.state.value,
                    userComments:this.state.textAreaValue
                })
            }
        )
        postAction("/ybt/wf/approveTask/doApproveBatch", arr).then(
            res => {
                if (res.success) {
                    this.props.hideHandleAllModal()
                    this.props.reloadTable()
                }
            }
        )
    }

    render() {
        return (
            <Modal
                title="批量审核"
                visible={this.props.handleAllModalStatus}
                onCancel = {this.props.hideHandleAllModal}
                onOk = {this.sure}
            >
                <Radio.Group onChange={this.radioOnChange} value={this.state.value}>
                    <Radio value={2}>通过</Radio>
                    <Radio value={3}>退回</Radio>
                </Radio.Group>

                <p style={{marginTop: "20px"}}>请输入审核意见：</p>
                <TextArea rows={4} onChange={this.textAreaOnChange} value = {this.state.textAreaValue} />

            </Modal>
        )
    }
}

export default HandleAllModal;